.single-todo {
  display: flex;
  border-radius: 15px;
  padding: 20px;
  margin-top: 10px;
  background-color: #5BC0BE;
  transition: 2ms;
  cursor: pointer;
}

.single-todo:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
  transform: scale(1.01);
}

.single-todo-text {
  flex: 1;
  padding: 5px;
  border: none;
  font-size: 20px;
  background-color: #5BC0BE;
}

.single-todo-text:focus {
  outline: solid 1px #3A506B;
  border-radius: 5px;
}

.icons-container {
  margin-top: 4px;
}

.icons {
  margin-left: 10px;
  font-size: 25px;
  cursor: pointer;
}

.icon-edit {
  color: #3a506b;
}

.icon-delete {
  color: #f94144;
}

.icon-done {
  color: #4d908e;
}

.drag {
  box-shadow: 0 0 20px rgba(0, 0, 0);
}